import { FC, PropsWithoutRef, useRef } from 'react';

import { Grid, ForwardProps, useI18n, useBreakpoint } from '@pega/cosmos-react-core';

import {
  StyledCaseSummaryFields,
  StyledSummaryPrimaryList,
  StyledSummarySecondaryList
} from './CaseView.styles';
import { CaseSummaryFieldsProps } from './CaseView.types';
import { useCaseViewContext } from './CaseView.context';

const CaseSummaryFields: FC<CaseSummaryFieldsProps & ForwardProps> = ({
  primary,
  secondary,
  inSummaryPanel
}: PropsWithoutRef<CaseSummaryFieldsProps>) => {
  const t = useI18n();
  const { testIds } = useCaseViewContext();
  const containerRef = useRef<HTMLDivElement>(null);
  const isSMContentWidthOrAbove = useBreakpoint('sm', {
    breakpointRef: containerRef,
    themeProp: 'content-width'
  });
  const wrapped = !inSummaryPanel && !isSMContentWidthOrAbove;

  return (
    <Grid
      data-testid={testIds.summaryFields}
      as={StyledCaseSummaryFields}
      container={{ rowGap: 2 }}
      ref={containerRef}
    >
      {primary && (
        <StyledSummaryPrimaryList
          variant='stacked'
          fields={primary}
          aria-label={t('primary_summary_fields')}
          wrapped={wrapped}
        />
      )}
      {secondary && (
        <StyledSummarySecondaryList
          variant={wrapped ? 'stacked' : 'inline'}
          fields={secondary}
          aria-label={t('summary_fields')}
        />
      )}
    </Grid>
  );
};

export default CaseSummaryFields;
