import { FunctionComponent } from 'react';

import { Icon, Flex, registerIcon, Text, useI18n, StyledIcon } from '@pega/cosmos-react-core';
import * as checkIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/check.icon';

import { StageProps } from './Stages.types';
import { StyledDateTimeDisplay, StyledStep, StyledStepsContainer } from './Stages.styles';

registerIcon(checkIcon);

export interface StageGlimpseProps {
  stage: StageProps;
}

const StageGlimpse: FunctionComponent<StageGlimpseProps> = ({ stage }: StageGlimpseProps) => {
  const t = useI18n();
  return (
    <>
      <div>
        {stage.date ? (
          <StyledDateTimeDisplay value={stage.date} variant='relative' format='short' />
        ) : (
          <Text variant='secondary'>{t('not_started')}</Text>
        )}
      </div>
      {stage.steps && (
        <StyledStepsContainer>
          {stage.steps.map(({ id, name, completed }) => (
            <Flex as={StyledStep} container={{ gap: 0.5 }} key={id} completed={completed}>
              {completed ? (
                <Icon name='check' />
              ) : (
                <StyledIcon as='span' role='presentation' aria-hidden='true'>
                  
                </StyledIcon>
              )}
              <div>{name}</div>
            </Flex>
          ))}
        </StyledStepsContainer>
      )}
    </>
  );
};

export default StageGlimpse;
