import { FunctionComponent } from 'react';
import styled, { css } from 'styled-components';

import { Card, Count, Flex, Icon, useI18n, VisuallyHiddenText } from '@pega/cosmos-react-core';

import { CaseViewProps } from './CaseView.types';

export interface UtilitiesSummaryProps {
  items?: CaseViewProps['utilitiesSummaryItems'];
}

export const StyledUtilitiesSummary = styled.div(({ theme }) => {
  return css`
    width: 2.75rem;
    padding: ${theme.base.spacing} 0;
  `;
});

const UtilitiesSummary: FunctionComponent<UtilitiesSummaryProps> = ({ items = [] }) => {
  const t = useI18n();
  return items.length ? (
    <Card
      container={{ direction: 'column', alignItems: 'center', gap: 3 }}
      as={StyledUtilitiesSummary}
      aria-label={t('utilities_summary')}
      role='list'
    >
      {items.map(item => {
        return (
          <Flex
            container={{
              direction: 'column',
              alignItems: 'center',
              gap: 0.5
            }}
            item={{ grow: 1 }}
            key={item.iconName}
          >
            <Icon name={item.iconName} />
            {item.count !== undefined && <Count aria-hidden>{item.count}</Count>}
            <VisuallyHiddenText role='listitem'>{`${item.count ?? ''} ${
              item.name
            }`}</VisuallyHiddenText>
          </Flex>
        );
      })}
    </Card>
  ) : null;
};

export default UtilitiesSummary;
